import Component from "@egjs/component";
import Viewport from "./components/Viewport";

import { merge, getProgress, toArray, parseElement, isString } from "./utils";
import { DEFAULT_OPTIONS, EVENTS, DIRECTION, AXES_EVENTS, STATE_TYPE, DEFAULT_MOVE_TYPE_OPTIONS } from "./consts";
import { FlickingOptions, FlickingEvent, Direction, EventType, FlickingPanel, TriggerCallback, FlickingContext, FlickingStatus, Plugin, ElementLike } from "./types";

/**
 * @memberof eg
 * @extends eg.Component
 * @support {"ie": "9+", "ch" : "latest", "ff" : "latest",  "sf" : "latest" , "edge" : "latest", "ios" : "7+", "an" : "4.X+"}
 * @requires {@link https://github.com/naver/egjs-component|eg.Component}
 * @requires {@link https://github.com/naver/egjs-axes|eg.Axes}
 * @see Easing Functions Cheat Sheet {@link http://easings.net/} <ko>  Cheat Sheet {@link http://easings.net/}</ko>
 * @throws {Error} An Error occur when given base element doesn't exist or it hasn't proper DOM structure to be initialized. <ko>        DOM    .</ko>
 */
class Flicking extends Component {
  /**
   * Version info string
   * @ko  
   * @example
   * eg.Flicking.VERSION;  // ex) 3.0.0
   * @memberof eg.Flicking
   */
  public static VERSION: string = "#__VERSION__#";
  /**
   * Direction constant - "PREV" or "NEXT"
   * @ko   - "PREV"  "NEXT"
   * @example
   * eg.Flicking.DIRECTION.PREV; // "PREV"
   * eg.Flicking.DIRECTION.NEXT; // "NEXT"
   */
  public static DIRECTION: Direction = DIRECTION;

  /**
   * Event types
   * @ko     
   */
  public static EVENTS: EventType = EVENTS;

  public options: FlickingOptions;

  private wrapper: HTMLElement;
  private viewport: Viewport;
  private eventContext: FlickingContext;
  private plugins: Plugin[] = [];

  /**
   * @param element A base element for the eg.Flicking module. When specifying a value as a `string` type, you must specify a css selector string to select the element.<ko>eg.Flicking    . `string`      css    .</ko>
   * @param options The option object of the eg.Flicking module<ko>eg.Flicking   </ko>
   * @param {string} [options.classPrefix="eg-flick"] A prefix for class names of the panels, viewport and camera.<ko> ,    .</ko>
   * @param {number} [options.deceleration=0.0075] Deceleration value for panel movement animation where acceleration is manually enabled by user. Higher value means shorter running time.<ko>       .      .</ko>
   * @param {boolean} [options.horizontal=true] Direction of panel movement. (true: horizontal, false: vertical)<ko>  . (true: , false: )</ko>
   * @param {boolean} [options.circular=false] Enables circular mode, which connects first/last panel for infinite scrolling<ko>  .          .</ko>
   * @param {number} [options.threshold=40] Movement threshold to destination panel(unit: pixel). A panel element must be dragged beyond the threshold to move to the destination panel.<ko>    (: ).         .</ko>
   * @param {number} [options.duration=100] Duration of the panel movement. (unit: ms)<ko>    .(: ms)</ko>
   * @param {function} [options.panelEffect=x => 1 - Math.pow(1 - x, 3)] The easing function to apply to a panel moving animation. The default function is easeOutCubic.<ko>    `easing`.  `easeOutCubic`.</ko>
   * @param {number} [options.defaultIndex=0] Index of panel to set as default when initializing the module. A zero-based integer.<ko>     , 0  .</ko>
   * @param {string[]} [options.inputType=["touch,"mouse"]] Types of input devices. ({@link https://naver.github.io/egjs-axes/release/latest/doc/eg.Axes.PanInput.html|eg.Axes.PanInput Reference})<br>- "touch": A touch input device.<br>- "mouse": A mouse.<ko>  . ({@link https://naver.github.io/egjs-axes/release/latest/doc/eg.Axes.PanInput.html|eg.Axes.PanInput })<br>- "touch":   .<br>- "mouse": .</ko>
   * @param {number} [options.thresholdAngle=45] The threshold value that determines whether user input is horizontal or vertical. (0 ~ 90)<ko>         (0 ~ 90)</ko>
   * @param {number|string|number[]|string[]} [options.bounce=[10,10]] The size value of the bounce area. Only can be enabled when `circular=false`<ko>  . `circular=false`    .</ko>
   * @param {Boolean} [options.adaptive=false] Whether the height(horizontal)/width(vertical) of the viewport element reflects the height/width value of the panel after completing the movement.<ko>      (horizontal)/(vertical)   /  .</ko>
   * @param {number} [options.zIndex=2000] z-index value for viewport element<ko>  z-index </ko>
   * @param {boolean} [options.bound=false] Prevents view going out of first/last panel. Only can be enabled when `circular=false`.<ko>       . `circular=false`    .</ko>
   * @param {boolean} [options.overflow=false] Disables CSS property `overflow: hidden` in viewport if `true`.<ko>`true`   `overflow: hidden`  .</ko>
   * @param {string} [options.hanger="50%"] Position of hanger in viewport, which hangs panel anchors.<br>Should be provided in px or % value of viewport size.<br>You can combinate those values with plus/minus sign<br>ex) "50", "100px", "0%", "25% + 100px"<ko>   .       .<br>px,    %   ,  +  -     .<br>) "50", "100px", "0%", "25% + 100px"</ko>
   * @param {string} [options.anchor="50%"] Position of anchor in panels, which can be hanged by viewport hanger.<br>Should be provided in px or % value of panel size.<br>You can combinate those values with plus/minus sign<br>ex) "50", "100px", "0%", "25% + 100px"<ko>   .           .<br>px,    %   ,  +  -     .<br>) "50", "100px", "0%", "25% + 100px"</ko>
   * @param {number} [options.gap=0] Space between each panels.<br>Should be given in number(px).<ko>     (px)</ko>
   * @param {number} [options.snap=1] The number of panels you're going to roll over to when you snap<ko>            </ko>
   * @param {boolean} [options.freeScroll=false] If true, panels can scroll freely when flicked.<ko>         .</ko>
   */
  constructor(
    element: ElementLike,
    options: Partial<FlickingOptions> = {},
  ) {
    super();

    // Set flicking wrapper user provided
    let wrapper: HTMLElement | null;
    if (isString(element)) {
      wrapper = document.querySelector(element);
      if (!wrapper) {
        throw new Error("Base element doesn't exist.");
      }
    } else if (element.nodeName && element.nodeType === 1) {
      wrapper = element;
    } else {
      throw new Error("Element should be provided in string or HTMLElement.");
    }

    this.wrapper = wrapper;
    // Override default options
    this.options = merge({}, DEFAULT_OPTIONS, options) as FlickingOptions;
    // Override moveType option
    const currentOptions = this.options;
    const moveType = currentOptions.moveType;

    if (moveType in DEFAULT_MOVE_TYPE_OPTIONS) {
      currentOptions.moveType = DEFAULT_MOVE_TYPE_OPTIONS[moveType as keyof typeof DEFAULT_MOVE_TYPE_OPTIONS];
    }
    this.build();
  }
  /**
   * Move to the previous panel. If `horizontal=true`is left panel. If `horizontal=false`is upper panel.
   * @ko   . `horizontal=true`  . `horizontal=false`  .
   * @param [duration=options.duration] Duration of the panel movement (unit: ms) <ko>    (: ms)</ko>
   * @return An instance of a module itself <ko>  </ko>
   */
  public prev(duration?: number): this {
    const options = this.options;
    const viewport = this.viewport;
    const panelManager = viewport.panelManager;
    const currentIndex = viewport.getCurrentIndex();
    const indexRange = panelManager.getRange();
    const panelCount = panelManager.getPanelCount();
    const lastIndex = panelManager.getLastIndex();
    const minimumRange = options.infinite
      ? 0
      : indexRange.min;
    let prevIndex = currentIndex - 1;

    if (prevIndex < minimumRange) {
      prevIndex = this.options.circular && panelCount > 0
        ? options.infinite
          ? lastIndex
          : indexRange.max
        : -1;
    }

    return this.moveTo(prevIndex, duration);
  }

  /**
   * Move to the next panel. If `horizontal=true`is right panel. If `horizontal=false`is lower panel.
   * @ko   . `horizontal=true`  . `horizontal=false`  .
   * @param [duration=options.duration] Duration of the panel movement (unit: ms) <ko>    (: ms)</ko>
   * @return An instance of a module itself <ko>  </ko>
   */
  public next(duration?: number): this {
    const options = this.options;
    const viewport = this.viewport;
    const panelManager = viewport.panelManager;
    const currentIndex = viewport.getCurrentIndex();
    const indexRange = panelManager.getRange();
    const panelCount = panelManager.getPanelCount();
    const lastIndex = panelManager.getLastIndex();
    const maximumRange = options.infinite
      ? lastIndex
      : indexRange.max;
    let nextIndex = currentIndex + 1;

    if (nextIndex > maximumRange) {
      nextIndex = options.circular && panelCount > 0
        ? options.infinite
          ? 0
          : indexRange.min
        : -1;
    }

    return this.moveTo(nextIndex, duration);
  }

  /**
   * Moves to the panel in the order specified in `index`. If `index` is equal to selected panel's index, no action is taken.
   * @ko `index`    . `index`     ,    .
   * @param index The index number of the panel to be moved.<ko>   .</ko>
   * @param duration [duration=options.duration] Duration of the panel movement (unit: ms) <ko>    (: ms)</ko>
   * @return An instance of a module itself <ko>  </ko>
   */
  public moveTo(index: number, duration?: number): this {
    const viewport = this.viewport;
    const panel = viewport.panelManager.get(index);
    const state = viewport.stateMachine.getState();

    if (!panel || state.type !== STATE_TYPE.IDLE) {
      return this;
    }

    const anchorPosition = panel.getAnchorPosition();
    const hangerPosition = viewport.getHangerPosition();

    let offset = 0;
    if (this.options.circular) {
      const scrollAreaSize = viewport.getScrollAreaSize();
      // Check all three possible locations, find the nearest position among them.
      const possiblePositions = [
        anchorPosition - scrollAreaSize,
        anchorPosition,
        anchorPosition + scrollAreaSize,
      ];
      const nearestPosition = possiblePositions.reduce((nearest, current) => {
        return (Math.abs(current - hangerPosition) < Math.abs(nearest - hangerPosition))
          ? current
          : nearest;
      }, Infinity);

      offset = nearestPosition - anchorPosition;
    }
    const currentIndex = this.getIndex();

    if (hangerPosition === anchorPosition + offset && currentIndex === index) {
      return this;
    }

    const eventType = panel.getIndex() === viewport.getCurrentIndex()
      ? EVENTS.NONE
      : EVENTS.CHANGE;

    viewport.moveTo(
      panel,
      eventType,
      null,
      offset,
      duration,
    );
    return this;
  }

  /**
   * Returns the index number of the selected panel.
   * @ko      .
   * @return Zero-based index number of the current panel element.<ko>   . 0  .</ko>
   */
  public getIndex(): number {
    return this.viewport.getCurrentIndex();
  }

  /**
   * Returns the wrapper element user provided in Flicking constructor.
   * @ko      .
   * @return Wrapper element user provided.<ko>   </ko>
   */
  public getElement(): HTMLElement {
    return this.wrapper;
  }

  /**
   * Returns the selected panel object
   * @ko     .
   * @return Selected panel object.<ko>  </ko>
   */
  public getCurrentPanel(): FlickingPanel | null {
    const viewport = this.viewport;
    const panel = viewport.getCurrentPanel();
    return panel
      ? viewport.castToFlickingPanel(panel)
      : null;
  }

  /**
   * Returns the panel object of given index
   * @ko      .
   * @return panel object of given index, `null` if it doesn't exists.<ko>    ,      `null`.</ko>
   */
  public getPanel(index: number): FlickingPanel | null {
    const viewport = this.viewport;
    const panel = viewport.panelManager.get(index);
    return panel
      ? viewport.castToFlickingPanel(panel)
      : null;
  }
  /**
   * Returns all panel objects in flicking.
   * @ko       .
   * @param - Check whether to include clone or not <ko>      </ko>
   * @return All panel objects <ko>     </ko>
   */
  public getAllPanels(includeClone?: boolean): FlickingPanel[] {
    const viewport = this.viewport;
    const panelManager = viewport.panelManager;
    const panels = includeClone
      ? panelManager.allPanels()
      : panelManager.originalPanels();

    return panels
      .filter(panel => !!panel)
      .map(panel => viewport.castToFlickingPanel(panel));
  }
  /**
   * Returns the panel objects shown in the flicking area.
   * @ko      .
   * @return The panel objects shown in the flicking area. <ko>    </ko>
   */
  public getVisiblePanels(): FlickingPanel[] {
    return this.getAllPanels(true).filter(panel => {
      const outsetProgress = panel.getOutsetProgress();

      return outsetProgress > -1 && outsetProgress < 1;
    });
  }
  /**
   * Returns the total length of original panels
   * @ko    .
   * @return Length of original panels.<ko>  </ko>
   */
  public getPanelCount(): number {
    return this.viewport.panelManager.getPanelCount();
  }

  /**
   * Set last panel index for `infinite' mode. [needPanel]{@link eg.Flicking#events:needPanel} won't be triggered anymore when last panel's index reaches it. Also, you can't add more panels after it.
   * @ko `infinite`      .          [needPanel]{@link eg.Flicking#events:needPanel}   . ,        .
   * @param - Last panel index.
   * @see {@link eg.Flicking.FlickingOptions}
   */
  public setLastIndex(index: number): void {
    this.viewport.setLastIndex(index);
  }

  /**
   * Checks whether the animated panel is playing.
   * @ko      .
   * @return Indicates whether the animated panel is playing <ko>     </ko>
   */
  public isPlaying(): boolean {
    return this.viewport.stateMachine.getState().playing;
  }

  /**
   * The input from the input device is not blocked so that the panel can be moved by the input device.
   * @ko     .
   * @return  An instance of a module itself <ko>  </ko>
   */
  public enableInput(): this {
    this.viewport.enable();

    return this;
  }

  /**
   * The input from the input device is blocked so that the panel is not moved by the input device.
   * @ko          .
   * @return An instance of a module itself <ko>  </ko>
   */
  public disableInput(): this {
    this.viewport.disable();

    return this;
  }

  /**
   * Get current flicking status. If the returned value is specified as a [setStatus()]{@link eg.Flicking#setStatus} method argument, it can be returned to its value status.
   * @ko    .   [setStatus()]{@link eg.Flicking#setStatus}         .
   * @return An object with current status value information.<ko>    .</ko>
   */
  public getStatus(): Readonly<FlickingStatus> {
    const viewport = this.viewport;

    const panels = viewport.panelManager.originalPanels()
      .filter(panel => !!panel)
      .map(panel => {
        return {
          html: panel.getElement().outerHTML,
          index: panel.getIndex(),
        };
      });

    return {
      index: viewport.getCurrentIndex(),
      panels,
      position: viewport.getCameraPosition(),
    };
  }

  /**
   * Restore to the state of the `status`.
   * @ko `status`  .
   * @param status Status value to be restored. You can specify the return value of the [getStatus()]{@link eg.Flicking#getStatus} method.<ko>  . [getStatus()]{@link eg.Flicking#getStatus}   .</ko>
   */
  public setStatus(status: FlickingStatus): void {
    this.viewport.restore(status);
  }

  /**
   * Add plugins that can have different effects on Flicking
   * @ko        .
   * @param - The plugin(s) to add <ko> ()</ko>
   * @return An instance of a module itself <ko>  </ko>
   */
  public addPlugins(plugins: Plugin | Plugin[]) {
    const newPlugins = ([] as Plugin[]).concat(plugins);

    newPlugins.forEach(plugin => {
      plugin.init(this);
    });

    this.plugins = this.plugins.concat(newPlugins);
    return this;
  }
  /**
   * Remove plugins from Flicking
   * @ko   .
   * @param - The plugin(s) to remove <ko> ()</ko>
   * @return An instance of a module itself <ko>  </ko>
   */
  public removePlugins(plugins: Plugin | Plugin[]) {
    const currentPlugins = this.plugins;
    const removedPlugins = ([] as Plugin[]).concat(plugins);

    removedPlugins.forEach(plugin => {
      const index = currentPlugins.indexOf(plugin);

      if (index > -1) {
        currentPlugins.splice(index, 1);
      }

      plugin.destroy(this);
    });
    return this;
  }

  /**
   * Return the reference element and all its children to the state they were in before the instance was created. Remove all attached event handlers. Specify `null` for all attributes of the instance (including inherited attributes).
   * @ko         .     .   ( ) `null` .
   * @example
   * const flick = new eg.Flicking("#flick");
   * flick.destroy();
   * console.log(flick.moveTo); // null
   */
  public destroy(): void {
    this.off();

    this.viewport.destroy();

    this.plugins.forEach(plugin => {
      plugin.destroy(this);
    });

    // release resources
    for (const x in this) {
      (this as any)[x] = null;
    }
  }

  /**
   * The horizontal or vertical length of the panel is updated according to the base element. If `horizontal=true` is horizontal. If `horizontal=false` is vertical.
   * @ko        . `horizontal=true` , `horizontal=false` .
   * @return An instance of a module itself<ko>  </ko>
   */
  public resize(): this {
    const viewport = this.viewport;

    viewport.panelManager.allPanels()
      .forEach(panel => panel.reset());
    viewport.resize();

    return this;
  }

  /**
   * Add new panel at the start.
   * @ko     .
   * @param element - Either HTMLElement, HTML string, or array of them.<br>It can be also HTML string of multiple elements with same depth.<ko>HTMLElement  HTML ,    .<br>,  depth     HTML  .</ko>
   * @return Array of appended panels.<ko>  </ko>
   * @example
   * // Suppose there were no panels at initialization
   * const flicking = new eg.Flicking("#flick");
   * flicking.replace(3, document.createElement("div")); // Add new panel at index 3
   * flicking.prepend("\<div\>Panel\</div\>"); // Prepended at index 2
   * flicking.prepend(["\<div\>Panel\</div\>", document.createElement("div")]); // Prepended at index 0, 1
   */
  public prepend(element: ElementLike | ElementLike[]): FlickingPanel[] {
    const viewport = this.viewport;
    const parsedElements = parseElement(element);

    const insertingIndex = Math.max(viewport.panelManager.getRange().min - parsedElements.length, 0);
    return viewport.insert(insertingIndex, parsedElements);
  }

  /**
   * Add new panel at the end.
   * @ko     .
   * @param element - Either HTMLElement, HTML string, or array of them.<br>It can be also HTML string of multiple elements with same depth.<ko>HTMLElement  HTML ,    .<br>,  depth     HTML  .</ko>
   * @return Array of appended panels.<ko>  </ko>
   * @example
   * // Suppose there were no panels at initialization
   * const flicking = new eg.Flicking("#flick");
   * flicking.append(document.createElement("div")); // Appended at index 0
   * flicking.append("\<div\>Panel\</div\>"); // Appended at index 1
   * flicking.append(["\<div\>Panel\</div\>", document.createElement("div")]); // Appended at index 2, 3
   * // Even this is possible
   * flicking.append("\<div\>Panel 1\</div\>\<div\>Panel 2\</div\>"); // Appended at index 4, 5
   */
  public append(element: ElementLike | ElementLike[]): FlickingPanel[] {
    const viewport = this.viewport;

    return viewport.insert(viewport.panelManager.getRange().max + 1, element);
  }

  /**
   * Replace panels with new panels from given index. If index is empty, add new panel at target index.
   * @ko      .    ,     .
   * @param index - Start index to replace new panels.<ko>    </ko>
   * @param element - Either HTMLElement, HTML string, or array of them.<br>It can be also HTML string of multiple elements with same depth.<ko>HTMLElement  HTML ,    .<br>,  depth     HTML  .</ko>
   * @return Array of created panels by replace.<ko>    </ko>
   * @example
   * // Suppose there were no panels at initialization
   * const flicking = new eg.Flicking("#flick");
   *
   * // This will add new panel at index 3,
   * // Index 0, 1, 2 is empty at this moment.
   * // [empty, empty, empty, PANEL]
   * flicking.replace(3, document.createElement("div"));
   *
   * // As index 2 was empty, this will also add new panel at index 2.
   * // [empty, empty, PANEL, PANEL]
   * flicking.replace(2, "\<div\>Panel\</div\>");
   *
   * // Index 3 was not empty, so it will replace previous one.
   * // It will also add new panels at index 4 and 5.
   * // before - [empty, empty, PANEL, PANEL]
   * // after - [empty, empty, PANEL, NEW_PANEL, NEW_PANEL, NEW_PANEL]
   * flicking.replace(3, ["\<div\>Panel\</div\>", "\<div\>Panel\</div\>", "\<div\>Panel\</div\>"])
   */
  public replace(index: number, element: ElementLike | ElementLike[]): FlickingPanel[] {
    return this.viewport.replace(index, element);
  }

  /**
   * Remove panel at target index. This will decrease index of panels behind it.
   * @ko `index`    .  `index`    .
   * @param index - Index of panel to remove.<ko>  </ko>
   * @param {number} [deleteCount=1] - Number of panels to remove from index.<ko>`index`    .</ko>
   * @return Array of removed panels<ko>  </ko>
   */
  public remove(index: number, deleteCount: number = 1): FlickingPanel[] {
    return this.viewport.remove(index, deleteCount);
  }

  private build(): void {
    this.initViewport();
    this.listenInput();
    this.listenResize();
  }

  private initViewport(): void {
    const wrapper = this.wrapper;
    const options = this.options;
    const cameraElement = document.createElement("div");

    // Make all panels to be a child of camera element
    // wrapper <- viewport <- camera <- panels[1...n]
    toArray(wrapper.children).forEach(child => {
      cameraElement.appendChild(child);
    });

    // Clipping area for camera element
    const viewportElement = document.createElement("div");
    viewportElement.appendChild(cameraElement);

    // Add viewport element to wrapper
    wrapper.appendChild(viewportElement);

    // Make viewport instance with panel container element
    this.viewport = new Viewport(this, viewportElement, cameraElement, options, this.triggerEvent);
  }

  private listenInput(): void {
    const flicking = this;
    const viewport = flicking.viewport;
    const stateMachine = viewport.stateMachine;

    // Set event context
    flicking.eventContext = {
      flicking,
      viewport: flicking.viewport,
      transitTo: stateMachine.transitTo,
      triggerEvent: flicking.triggerEvent,
      moveCamera: flicking.moveCamera,
      stopCamera: viewport.stopCamera,
    };

    const handlers = {};
    for (const key in AXES_EVENTS) {
      const eventType = AXES_EVENTS[key];

      handlers[eventType] = (e: any) => stateMachine.fire(eventType, e, flicking.eventContext);
    }

    // Connect Axes instance with PanInput
    flicking.viewport.connectAxesHandler(handlers);
  }

  private listenResize(): void {
    if (this.options.autoResize) {
      window.addEventListener("resize", () => {
        this.resize();
      });
    }
  }

  private triggerEvent = <T extends FlickingEvent>(
    eventName: string,
    axesEvent: any,
    isTrusted: boolean,
    params: Partial<T> = {},
  ): TriggerCallback => {
    const viewport = this.viewport;

    let canceled: boolean = true;

    // Ignore events before viewport is initialized
    if (viewport) {
      const state = viewport.stateMachine.getState();
      const { prev, next } = viewport.getScrollArea();
      const pos = viewport.getCameraPosition();
      let progress = getProgress(pos, [prev, prev, next]);

      if (this.options.circular) {
        progress %= 1;
      }
      canceled = !super.trigger(eventName, merge({
        type: eventName,
        index: this.getIndex(),
        panel: this.getCurrentPanel(),
        direction: state.direction,
        holding: state.holding,
        progress,
        axesEvent,
        isTrusted,
      }, params));
    }

    return {
      onSuccess(callback: () => void): TriggerCallback {
        if (!canceled) {
          callback();
        }
        return this;
      },
      onStopped(callback: () => void): TriggerCallback {
        if (canceled) {
          callback();
        }
        return this;
      },
    } as TriggerCallback;
  }

  // Return result of "move" event triggered
  private moveCamera = (axesEvent: any): TriggerCallback => {
    const viewport = this.viewport;
    const state = viewport.stateMachine.getState();
    const options = this.options;

    const pos = axesEvent.pos.flick;
    const previousPosition = viewport.getCameraPosition();

    if (axesEvent.isTrusted && state.holding) {
      const inputOffset = options.horizontal
        ? axesEvent.inputEvent.offsetX
        : axesEvent.inputEvent.offsetY;

      const isNextDirection = inputOffset < 0;

      let cameraChange = pos - previousPosition;
      const looped = isNextDirection === (pos < previousPosition);
      if (options.circular && looped) {
        // Reached at max/min range of axes
        const scrollAreaSize = viewport.getScrollAreaSize();
        cameraChange = -Math.sign(cameraChange) * (scrollAreaSize - Math.abs(cameraChange));
      }

      const currentDirection = cameraChange === 0
        ? state.direction
        : cameraChange > 0
          ? DIRECTION.NEXT
          : DIRECTION.PREV;

      state.delta += cameraChange;
      state.direction = currentDirection;
    }

    viewport.moveCamera(pos, axesEvent);
    return this.triggerEvent(EVENTS.MOVE, axesEvent, axesEvent.isTrusted)
      .onStopped(() => {
        // Undo camera movement
        viewport.moveCamera(previousPosition, axesEvent);
      });
  }
}

export default Flicking;
